<template>
  <AuthContainer
    :current-step="currentStep"
    :steps-count="stepsCount"
    class="admin-login"
  >
    <keep-alive>
      <component
        :is="component"
        @submit="onNext"
      />
    </keep-alive>
  </AuthContainer>
</template>

<script>
import { useRoute, useRouter } from 'vue-router'
import { computed, defineAsyncComponent, ref, watchEffect } from 'vue'
import AuthContainer from '@/admin/views/Auth/AuthContainer'

export default {
  name: 'Login',
  components: {
    AuthContainer,
    Step1: defineAsyncComponent(() => import('@/admin/views/Auth/Login/Steps/Step1')),
    Step2: defineAsyncComponent(() => import('@/admin/views/Auth/Login/Steps/Step2'))
  },
  setup () {
    const route = useRoute()
    const router = useRouter()
    const stepsCount = ref(2)
    const currentStep = ref(Number(route.query.step || 1))

    const component = computed(() => {
      return 'Step' + currentStep.value
    })

    watchEffect(() => {
      currentStep.value = Number(route.query.step || 1)
    })

    const onNext = () => {
      const newStep = currentStep.value + 1
      currentStep.value = newStep > stepsCount.value ? stepsCount.value : newStep
      router.push({
        ...route,
        query: {
          step: newStep
        }
      })
    }

    return {
      component,
      stepsCount,
      currentStep,
      onNext
    }
  }
}
</script>

